<!-- 维修记录表 只展示维修记录信息 无操作 -->
<template>
  <view class="make-maint-record padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="维修记录表"></u-navbar>
    <view>
      <nnkjc-detail-group>
        <nnkjc-detail-item
            detailTitle="维修类型"
            :align="'left'"
            :gap="false"
        >
          <view slot="content">
            <text>{{ typeMap[form.assetFlag] }}</text>
          </view>
        </nnkjc-detail-item>
      </nnkjc-detail-group>
<!--      是资产维修-->
      <view v-if="form.assetFlag === '1'">
        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="设备编号"
              :align="'left'"
          >
            <view slot="content">
              <text>{{ assetAbout.code }}</text>
            </view>
          </nnkjc-detail-item>
          <nnkjc-detail-item
              detailTitle="设备名称"
              :align="'left'"
          >
            <view slot="content">
              <text>{{ assetAbout.name }}</text>
            </view>
          </nnkjc-detail-item>
          <nnkjc-detail-item
              detailTitle="规格型号"
              :align="'left'"
          >
            <view slot="content">
              <text>{{ assetAbout.modelName }}</text>
            </view>
          </nnkjc-detail-item>
          <nnkjc-detail-item
              detailTitle="设备房"
              :align="'left'"
              :gap="false"
          >
            <view slot="content">
              <text>{{ assetAbout.installPos }}</text>
            </view>
          </nnkjc-detail-item>
        </nnkjc-detail-group>
      </view>

      <nnkjc-detail-group>
        <nnkjc-detail-item
            detailTitle="维修处理"
            :align="'left'"
            :gap="false"
        >
          <view slot="content">
            <text>{{ maintTypeMap[form.maintType] }}</text>
          </view>
        </nnkjc-detail-item>
      </nnkjc-detail-group>

      <view class="margin-normal">
        <common-header :title="'维修日志'"></common-header>
      </view>

      <view v-if="mainted && maintRecord.length">
        <nnkjc-detail-group v-for="(record, index) in maintRecord" :key="record.id">
          <nnkjc-detail-item
              detailTitle="维修日志"
              :align="'left'"
              :gap="false"
          >
            <view slot="content">
              <text>{{ record.content }}</text>
            </view>
          </nnkjc-detail-item>

          <view style="padding-bottom: 10px; border-bottom: 1px solid #f1f1f1;">
            <u-row gutter="12">
              <u-col span="2" v-for="(image, index_) in record.maintPicReal" :key="index_">
                <u-image
                    @click="doPreviewImage(record.maintPicReal, index_)"
                    width="50px"
                    height="50px"
                    border-radius="6"
                    :src="image"></u-image>
              </u-col>
            </u-row>
          </view>

          <nnkjc-detail-item
              detailTitle="维修时间"
              :align="'left'"
          >
            <view slot="content">
              <text>{{ record.maintTime }}</text>
            </view>
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="维修人"
              :align="'left'"
              :gap="false"
          >
            <view slot="content">
              <text>{{ record.maintStaffName }}</text>
            </view>
          </nnkjc-detail-item>
        </nnkjc-detail-group>
      </view>
    </view>

    <u-toast ref="uToast"/>
  </view>
</template>

<script>
  import CONFIG from "../../config";

  export default {
    name: "maint-record-view",
    data() {
      return {
        maintRecord: [],
        maintStaffMap: {},
        mainted: false,  //  是否已经维修过
        loading: false,
        imageUrl: [],
        valueResult: "",
        resultMap: {
          "0": "未完成",
          "1": "维修完成"
        },
        assetAbout: {
          assetId: "",
          name: "",
          code: "",
          modelName: "",
          installPos: ""
        },
        form: {
          taskId: "",
          sendId: "",
          assetFlag: "1",
          assetId: "",
          maintType: "in",
          finishFlag: "0",
          code: "",
          content: "",
          maintPic: "",
        },
        typeMap: {
          "0": "非资产维修",
          "1": "资产维修"
        },
        maintTypeMap: {
          in: "自理",
          out: "外委"
        },
        background: {
          'background': '#f2f2f8'
        }
      }
    },
    onLoad(options) {
      this.form.taskId = options.taskId;
      this.form.sendId = options.sendId;
      this.getMaintList();//  获取维修记录
    },
    methods: {
      back() {
        uni.navigateBack({
          delta: 1
        });
      },
      //  获取维修的详情
      getMaintList() {
        this.$u
            .post('/maint/getMaintList', {
              taskId: this.form.taskId
            }).then(res => {
          if (res.code === 200 && res.data) {
            this.mainted = true;
            if (res.data.recordList) {
              res.data.recordList.forEach(record => {
                record.maintPicReal =
                    record.maintPic.split(',')
                        .filter((pic) => {
                          return !!(pic);
                        }).map(pic => {
                      return CONFIG.baseUrl + '/' + pic;
                    })
              })
              this.maintRecord = res.data.recordList;
              this.form.assetFlag= res.data.assetFlag;
              this.form.maintType = res.data.maintType;
              this.form.finishFlag = res.data.finishFlag;
            }
            if(res.data.assetFlag === '1') {
              this.assetAbout.code = res.data.assetCode;
              this.assetAbout.name = res.data.assetName;
              this.assetAbout.modelName = res.data.assetModel;
              this.assetAbout.installPos = res.data.assetPos;
            }
          } else {
            this.mainted = false;
          }
        }).catch(e => {
        })
      },
      //  图片预览
      doPreviewImage(images, index) {
        uni.previewImage({
          urls: images,
          current: index,
          success: () => {
          },
          fail: () => {
            uni.showToast({
              title: '预览图片失败',
              icon: 'none'
            });
          }
        });
      }
    }
  }
</script>

