  .add-btn {
    margin-top: 10px;
    width: 50px;
    height: 50px;
    border-radius: 5px;
    border: 1px dashed #f1f1f1;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .add-new {
    height: 20px;
    font-size: 14px;
    font-weight: 400;
    line-height: 12px;
    color: #409eff;
  }

  .u-btn--error--plain {
    background-color: #ffffff !important;
  }
</style>